import { createContext, useContext, useEffect, useState } from "react";
import { auth } from "../firebase";
import { 
  createUserWithEmailAndPassword, 
  signInWithEmailAndPassword, 
  signOut, 
  onAuthStateChanged,
  sendPasswordResetEmail
} from "firebase/auth";

const AuthContext = createContext();

export function AuthProvider({ children }) {
  const [currentUser, setCurrentUser] = useState(null);
  const [loading, setLoading] = useState(true);

  function signup(email, password) {
    return createUserWithEmailAndPassword(auth, email, password);
  }


  async function isAdminUser(email) {
  const adminEmails = ['pawartushar710@gmail.com'];
  return adminEmails.includes(email);
}

  async function isVendorManager(email) {
    const vendorManagerEmails = ['pawartushar710@gmail.com', 'vendor.manager@company.com'];
    return vendorManagerEmails.includes(email);
  }

  async function getUserRole(email) {
    if (await isAdminUser(email)) return 'admin';
    if (await isVendorManager(email)) return 'vendor_manager';
    return 'user';
  }

  function login(email, password) {
    return signInWithEmailAndPassword(auth, email, password);
  }

  function logout() {
    return signOut(auth);
  }

  function resetPassword(email) {
    return sendPasswordResetEmail(auth, email);
  }

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      setCurrentUser(user);
      setLoading(false);
    });
    return unsubscribe;
  }, []);


  const value = {
  currentUser,
  signup,
  login,
  logout,
  resetPassword,
  isAdminUser,
  isVendorManager,
  getUserRole
};

  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  return useContext(AuthContext);
}